.text {
  font-family: var(--font-family-barlow-condensed);
  font-size: 16px;
  line-height: 19px;
  text-align: center;
  letter-spacing: 2.7px;
  text-transform: uppercase;
}
.textColor {
  color: #ffffff;
  mix-blend-mode: normal;
  opacity: 0.25;
  margin-right: 18px;
}
.image {
  width: 170px;
  height: 170px;
}
